* {
  box-sizing: border-box;
  margin: 0;
}

#root {
  height: 100%;
  background-color: #fdfdfd;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

hr {
  width: 100%;
  border: 0;
  border-top: 1px solid #ddd;
}

.page-header {
  padding: 3%;
  display: flex;
}

.page-header-left, .page-header-center, .page-header-right {
  align-items: center;
  display: flex;
}

.page-header-left {
  flex: 1;
  justify-content: flex-start;
}

.page-header-center {
  flex: 2;
  justify-content: center;
}

.page-header-right {
  flex: 1;
  justify-content: flex-end;
}

.page-title {
  color: #0f0f0f;
  text-transform: uppercase;
  font-family: Palatino, Verdana, Times New Roman;
  font-size: 40px;
  line-height: .8;
}

.instagram-icon {
  vertical-align: middle;
}

.page-content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding: 0 10%;
  display: flex;
}

.about {
  text-align: center;
  font-family: Palatino, Verdana, Times New Roman;
  font-size: 20px;
}

.designs {
  justify-content: space-evenly;
  gap: 5%;
  padding: 0 5%;
  display: flex;
}

.designs-image {
  height: 100%;
  max-height: 480px;
  width: 100%;
  max-width: 480px;
  border: 1px solid #888;
  border-radius: 5px;
  flex: 1;
}

.designs-info {
  color: #0f0f0f;
  flex-direction: column;
  flex: 1;
  justify-content: space-around;
  align-items: flex-start;
  gap: 15px;
  font-family: Palatino, Verdana, Times New Roman;
  font-size: 20px;
  display: flex;
}

.purchase-buttons {
  width: 100%;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  display: flex;
}

.purchase-button {
  width: fit-content;
  color: #fff;
  text-transform: uppercase;
  border-radius: 20px;
  padding: 8px 14px;
  font-size: 14px;
  text-decoration: none;
}

.purchase-button-strikkeland {
  background-color: #4c72e2;
}

.purchase-button-strikkeland:hover {
  background-color: #6086f6;
}

.purchase-button-ravelry {
  background-color: #de7266;
}

.purchase-button-ravelry:hover {
  background-color: #f2867a;
}

.instagram-feed {
  width: 100%;
  max-width: 750px;
}

.instagram-feed a {
  text-decoration: none;
}

.instagram-feed a:hover {
  text-decoration: underline;
}

.instagram-feed h1 {
  color: #0f0f0f;
  text-align: center;
  text-transform: uppercase;
  font-family: Palatino, Verdana, Times New Roman;
  font-size: 25px;
}

.page-footer {
  text-transform: uppercase;
  justify-content: center;
  padding: 3%;
  font-family: Palatino, Verdana, Times New Roman;
  font-size: 14px;
  display: flex;
}

@media (max-width: 480px) {
  #root {
    justify-content: space-around;
  }

  .page-header-left, .page-header-right {
    display: none;
  }

  .page-title {
    font-size: 32px;
  }

  .page-content {
    padding: 0 3%;
  }

  .about {
    font-size: 16px;
  }

  .designs {
    flex-direction: column-reverse;
  }

  .designs-info {
    font-size: 16px;
  }

  .designs-info h1 {
    font-size: 24px;
  }

  .instagram-feed h1 {
    font-size: 20px;
  }

  .purchase-buttons {
    flex-direction: column;
  }
}

